body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


/* ReactCSSAnimations */
/*----------------------------------------------*/
/* Profile */

.profile-enter {
  opacity: 0.01;
  -webkit-transition-delay: 400ms;
}

.profile-enter.profile-enter-active {
  opacity: 1;
  -webkit-transition: opacity 500ms;
  -webkit-transition-delay: 200ms;
}

.profile-appear {
  opacity: 0.01;
  -webkit-transition-delay: 2s;
}

.profile-appear.profile-appear-active {
  opacity: 1;
  -webkit-transition: opacity 500ms;
  -webkit-transition-delay: 2s;
}

.profile-leave {
  opacity: 1;
}

.profile-leave.profile-leave-active {
  opacity: 0;
  -webkit-transition: opacity 300ms;
}

/*----------------------------------------------*/
/* Resume */

.resumeAppear-appear {
  opacity: 0.01;
  -webkit-transition-delay: 300ms;
}

.resumeAppear-appear.resumeAppear-appear-active {
  opacity: 1;
  -webkit-transition: opacity 500ms;
  -webkit-transition-delay: 300ms;
}

.resumeAppear-leave {
  opacity: 1 !important;
}

.resumeAppear-leave.resumeAppear-leave-active {
  opacity: 0 !important;
  -webkit-transition: opacity 100ms;
}

/* Between Information panels */

.resumeTransition-appear {
  opacity: 0.01;
  -webkit-transition-delay: 80ms;
}

.resumeTransition-appear.resumeTransition-appear-active {
  opacity: 1;
  -webkit-transition: opacity 500ms;
  -webkit-transition-delay: 80ms;
}

.resumeTransition-leave {
  opacity: 1 !important;
}

.resumeTransition-leave.resumeTransition-leave-active {
  opacity: 0 !important;
  -webkit-transition: opacity 80ms;
}

/*----------------------------------------------*/
/* Works */

.worksAppear-appear {
  opacity: 0.01;
  -webkit-transition-delay: 300ms;
}

.worksAppear-appear.worksAppear-appear-active {
  opacity: 1;
  -webkit-transition: opacity 500ms;
  -webkit-transition-delay: 300ms;
}

.worksAppear-leave {
  opacity: 1 !important;
}

.worksAppear-leave.worksAppear-leave-active {
  opacity: 0 !important;
  -webkit-transition: opacity 100ms;
}

/* Between Information panels */

.worksTransition-appear {
  opacity: 0.01;
  -webkit-transition-delay: 80ms;
}

.worksTransition-appear.worksTransition-appear-active {
  opacity: 1;
  -webkit-transition: opacity 500ms;
  -webkit-transition-delay: 80ms;
}

.worksTransition-leave {
  opacity: 1 !important;
}

.worksTransition-leave.worksTransition-leave-active {
  opacity: 0 !important;
  -webkit-transition: opacity 80ms;
}

